<template>
  <div class="AgeInput">
    <h3 class="AgeInput-title">AGE</h3>
    <div class="AgeInput-settings">
      <button class="AgeInput-settings-button" @click="setAge(age - 1)">-</button>
      <span class="AgeInput-settings-age">{{ age }}</span>
      <button class="AgeInput-settings-button" @click="setAge(age + 1)">+</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AgeInput',
  computed: {
    age () {
      return this.$store.getters['age']
    }
  },
  methods: {
    setAge (age) {
      this.$store.dispatch('setAge', age)
    }
  }
}
</script>

