import Vue from 'vue'
import Vuex from 'vuex'
import storage from '../utils/storage'
import { getMaxBPM } from '../utils/helpers'

Vue.use(Vuex)

const state = {
  age: 29
}

const getters = {
  age: state => state.age,
  maxBPM: state => getMaxBPM(state.age)
}

const mutations = {
  setAge: (state, age) => (state.age = age)
}

const actions = {
  setAge: ({ commit }, age) => {
    if (age < 18 || age > 100) return

    commit('setAge', age)
    storage.setAge(age)
  },
  getAge: ({ commit }) => {
    const age = storage.getAge()

    if (age) {
      commit('setAge', parseInt(age))
    }
  }
}

const store = new Vuex.Store({ state, getters, mutations, actions })

export { store, state, getters, mutations, actions }
