<template>
  <div class="BPMData">
    <h3 class="BPMData-title">MAX BPM</h3>
    <div class="BPMData-max">{{ normalize(maxBPM) }}</div>
    <div class="BPMData-fractions">
      <template v-for="fraction in [6, 7, 8, 9]">
        <div :key="fraction">
          <h3 class="BPMData-title">{{ `${ fraction }0%` }}</h3>
          <div>{{ normalize(maxBPM * fraction / 10) }}</div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BPMData',
  computed: {
    maxBPM () {
      return this.$store.getters['maxBPM']
    }
  },
  methods: {
    normalize (num) {
      return num.toFixed(1)
    }
  }
}
</script>

<style lang="scss" scoped>
.BPMData {
  &-title {
    margin: 10px 0;
  }

  &-max {
    font-size: 25px;
  }

  &-fractions {
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
  }
}
